export default {
    props: {
        text: {
            // 按钮文字
            type: String,
            default: "做作业"
        },
        backgroundColor: {
            //用来判断按钮背景色的颜色，orange就是ff6c00渐变 gray就是灰色渐变
            type: String,
            default: "orange"
        },
        disabled:{
            type:Boolean,
            default:false
        }
    },
    methods: {
        clickBtn(e) {
            // 点击事件传递
            this.$emit("clickBtn", e.currentTarget.dataset.text);
        }
    }
};
